import { Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subscription, take } from 'rxjs';
import { ClientLocation } from '../../../models/client-location';
import { ClientLocationsService } from '../../../services/client-locations.service';
import { FilterPipe } from '../../../pipes/filter.pipe';
import { ProjectsCacheService } from 'src/app/cache-services/projects-cache.service';

@Component({
  selector: 'app-client-locations',
  templateUrl: './client-locations.component.html',
  styleUrls: ['./client-locations.component.scss'],
})
export class ClientLocationsComponent implements OnInit, OnDestroy {
  clientLocations: ClientLocation[] = [];

  editIndex: number;
  deleteIndex: number;

  searchBy: string = '';
  searchText: string;
  sortBy: string = 'clientLocationName';
  sortOrder: string = 'ASC';

  currentPageIndex: number = 0;
  searchPageIndex: number = 0;
  previousSearchPageIndex: number = 0;
  pages: any[] = [];
  pageSize: number = 7;

  newForm: FormGroup | any;
  editForm: FormGroup | any;
  deleteForm: FormGroup | any;

  @ViewChild('defaultTextBox_New') defaultTextBox_New: ElementRef;
  @ViewChild('defaultTextBox_Edit') defaultTextBox_Edit: ElementRef;
  @ViewChild('newClientLocationFormCancel') newClientLocationFormCancel: ElementRef;
  @ViewChild('editClientLocationFormCancel') editClientLocationFormCancel: ElementRef;

  subscription: Subscription;

  constructor(
    private clientLocationsService: ClientLocationsService,
    private cacheService: ProjectsCacheService,
    private formBuilder: FormBuilder
  ) {}

  ngOnInit() {
    this.subscription = this.clientLocationsService.getClientLocations().subscribe((response: ClientLocation[]) => {
      this.clientLocations = response;
      this.calculateNoOfPages();
    });

    this.newForm = this.formBuilder.group({
      clientLocationID: this.formBuilder.control(null),
      clientLocationName: this.formBuilder.control(null, [Validators.required, Validators.maxLength(50)]),
    });

    this.editForm = this.formBuilder.group({
      clientLocationID: this.formBuilder.control(null),
      clientLocationName: this.formBuilder.control(null, [Validators.required, Validators.maxLength(50)]),
    });

    this.deleteForm = this.formBuilder.group({
      clientLocationID: this.formBuilder.control(null),
      clientLocationName: this.formBuilder.control({ value: null, disabled: true }),
    });
  }

  calculateNoOfPages() {
    const filterPipe = new FilterPipe();
    const noOfPages = Math.ceil(
      filterPipe.transform(this.clientLocations, this.searchBy, this.searchText).length / this.pageSize
    );
    this.pages = [];

    for (let i = 0; i < noOfPages; i++) {
      this.pages.push({ pageIndex: i });
    }

    this.currentPageIndex = 0;
  }

  onPageIndexClicked(ind: number) {
    if (!this.searchText && ind >= 0 && ind < this.pages.length) {
      this.previousSearchPageIndex = ind;
      this.searchPageIndex = ind;
      this.currentPageIndex = ind;
    }
    this.searchPageIndex = this.previousSearchPageIndex;
    this.currentPageIndex = ind;
  }

  onNewClick() {
    this.newForm.reset({ clientLocationID: 0 });
    setTimeout(() => {
      this.defaultTextBox_New.nativeElement.focus();
    }, 10);
  }

  onSaveClick() {
    if (this.newForm.valid) {
      const found = this.clientLocations.some(
        (el) =>
          el.clientLocationName.split(/[ -]+/).join(' ').trim() ==
          this.newForm.value.clientLocationName.split(/[ -]+/).join(' ').trim()
      );
      if (found) return alert('That client location already exists!');

      this.newForm.value.clientLocationName =
        this.newForm.value.clientLocationName.charAt(0).toUpperCase().trimLeft() +
        this.newForm.value.clientLocationName.split(/[ ]+/).join(' ').slice(1);

      this.clientLocationsService
        .insertClientLocation(this.newForm.value)
        .pipe(take(1))
        .subscribe({
          next: (response) => {
            const p: ClientLocation | any = {};
            p.clientLocationID = response.clientLocationID;
            p.clientLocationName = response.clientLocationName;

            this.clientLocations.push(p);
            this.clientLocations = this.clientLocations.sort((a, b) =>
              a.clientLocationName > b.clientLocationName ? 1 : a.clientLocationName < b.clientLocationName ? -1 : 0
            );

            this.newForm.reset();
            this.newClientLocationFormCancel.nativeElement.click();
            this.calculateNoOfPages();
          },
          error: (error) => {
            console.log(error);
          },
        });
    }
  }

  onEditClick(clientLocation: ClientLocation) {
    this.editForm.reset();

    setTimeout(() => {
      this.editForm.patchValue(clientLocation);
      this.editIndex = this.clientLocations.indexOf(clientLocation);

      this.defaultTextBox_Edit.nativeElement.focus();
    }, 10);
  }

  onUpdateClick() {
    if (this.editForm.value) {
      const found = this.clientLocations.some(
        (el) =>
          el.clientLocationName.split(/[ -]+/).join(' ').trim() ==
          this.editForm.value.clientLocationName.split(/[ -]+/).join(' ').trim()
      );
      if (found) return alert('That client location already exists!');

      this.editForm.value.clientLocationName =
        this.editForm.value.clientLocationName.charAt(0).toUpperCase().trimStart() +
        this.editForm.value.clientLocationName.split(/[ ]+/).join(' ').slice(1);

      this.clientLocationsService
        .updateClientLocation(this.editForm.value)
        .pipe(take(1))
        .subscribe({
          next: (response: ClientLocation) => {
            this.cacheService.clear();
            this.clientLocations[this.editIndex] = response;

            this.clientLocations = this.clientLocations.sort((a, b) =>
              a.clientLocationName > b.clientLocationName ? 1 : a.clientLocationName < b.clientLocationName ? -1 : 0
            );

            this.editForm.reset();
            this.editClientLocationFormCancel.nativeElement.click();
          },
          error: (error) => {
            console.log(error);
          },
        });
    }
  }

  onDeleteClick(clientLocation: ClientLocation) {
    this.deleteForm.reset();

    setTimeout(() => {
      this.deleteForm.patchValue(clientLocation);
      this.deleteIndex = this.clientLocations.indexOf(clientLocation);
    }, 10);
  }

  onDeleteConfirmClick() {
    this.clientLocationsService
      .deleteClientLocation(this.deleteForm.value.clientLocationID)
      .pipe(take(1))
      .subscribe({
        next: () => {
          this.cacheService.clear();
          this.clientLocations.splice(this.deleteIndex, 1);

          this.deleteForm.reset();
          this.calculateNoOfPages();
        },
        error: (error) => {
          console.log(error);
        },
      });
  }

  onSearchTextChange() {
    this.calculateNoOfPages();
    if (!this.searchText) this.currentPageIndex = this.searchPageIndex;
    else this.currentPageIndex = 0;
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }
}
