import { Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, FormGroupDirective, Validators } from '@angular/forms';
import { Observable, Subscription, take } from 'rxjs';
import { ClientLocation } from '../../../models/client-location';
import { ClientLocationsService } from '../../../services/client-locations.service';
import { Project } from '../../../models/project';
import { ProjectsService } from '../../../services/projects.service';
import { FilterPipe } from '../../../pipes/filter.pipe';

@Component({
  selector: 'app-projects',
  templateUrl: './projects.component.html',
  styleUrls: ['./projects.component.scss'],
})
export class ProjectsComponent implements OnInit, OnDestroy {
  projects: Project[] = [];
  clientLocations: Observable<ClientLocation[]>;

  editIndex: number;
  deleteIndex: number;
  searchBy: string = '';
  searchText: string;

  currentPageIndex: number = 0;
  searchPageIndex: number = 0;
  previousSearchPageIndex: number = 0;
  pages: any[] = [];
  pageSize: number = 3;

  newForm: FormGroup | any;
  editForm: FormGroup | any;
  deleteForm: FormGroup | any;
  @ViewChild(FormGroupDirective) formDirective: FormGroupDirective;
  @ViewChild('newProjectFormCancel') newProjectFormCancel: ElementRef;
  @ViewChild('editProjectFormCancel') editProjectFormCancel: ElementRef;
  @ViewChild('disableAttr') disableAttr: ElementRef;

  subscription: Subscription;

  constructor(
    private projectsService: ProjectsService,
    private clientLocationsService: ClientLocationsService,
    private formBuilder: FormBuilder
  ) {}

  ngOnInit() {
    //new
    this.newForm = this.formBuilder.group({
      projectID: this.formBuilder.control(null, [
        Validators.required,
        Validators.pattern(/^[0-9]*$/),
        Validators.min(1),
      ]),
      projectName: this.formBuilder.control(null, [
        Validators.required,
        Validators.pattern(/^[A-Za-z0-9 /-]*$/),
        Validators.minLength(3),
        Validators.maxLength(30),
      ]),
      dateOfStart: this.formBuilder.control(null, [Validators.required]),
      teamSize: this.formBuilder.control(null, [Validators.pattern(/^[0-9]*$/), Validators.maxLength(7)]),
      active: this.formBuilder.control(null),
      status: this.formBuilder.control(null, [Validators.required]),
      clientLocationID: this.formBuilder.control(null, [Validators.required]),
    });

    this.editForm = this.formBuilder.group({
      projectID: this.formBuilder.control(null),
      projectName: this.formBuilder.control(null, [
        Validators.required,
        Validators.pattern(/^[A-Za-z0-9 /-]*$/),
        Validators.minLength(3),
        Validators.maxLength(30),
      ]),
      dateOfStart: this.formBuilder.control(null, [Validators.required]),
      teamSize: this.formBuilder.control(null, [Validators.pattern(/^[0-9]*$/), Validators.maxLength(7)]),
      active: this.formBuilder.control(null),
      status: this.formBuilder.control(null, [Validators.required]),
      clientLocationID: this.formBuilder.control(null, [Validators.required]),
    });

    this.deleteForm = this.formBuilder.group({
      projectID: this.formBuilder.control({ value: null, disabled: true }),
      projectName: this.formBuilder.control({ value: null, disabled: true }),
    });

    this.subscription = this.projectsService.getProjects().subscribe((response: Project[]) => {
      this.projects = response;

      this.calculateNoOfPages();
      this.currentPageIndex = 0;
    });

    this.clientLocations = this.clientLocationsService.getClientLocations();
  }

  onNewClick() {
    this.formDirective.resetForm();
    setTimeout(() => {
      this.newForm.get('active').setValue(true);
    });
  }

  onSaveClick() {
    if (this.newForm.valid) {
      this.projectsService
        .insertProject(this.newForm.value)
        .pipe(take(1))
        .subscribe((response) => {
          let p: Project | any = {};
          p.projectID = response.projectID;
          p.projectName = response.projectName;
          p.dateOfStart = response.dateOfStart;
          p.teamSize = response.teamSize;
          p.active = response.active;
          p.status = response.status;
          p.clientLocationID = response.clientLocationID;
          p.clientLocation = response.clientLocation;

          this.projects.push(p);

          this.newForm.reset();

          this.newProjectFormCancel.nativeElement.click();
          this.calculateNoOfPages();
        });
    }
  }

  onEditClick(index: number) {
    this.editForm.reset();
    this.disableAttr.nativeElement.disabled = true;

    setTimeout(() => {
      this.editForm.get('projectID').setValue(this.projects[index].projectID);
      this.editForm.get('projectName').setValue(this.projects[index].projectName);
      this.editForm.get('dateOfStart').setValue(this.projects[index].dateOfStart.split('/').reverse().join('-'));
      this.editForm.get('teamSize').setValue(this.projects[index].teamSize);
      this.editForm.get('active').setValue(this.projects[index].active);
      this.editForm.get('status').setValue(this.projects[index].status);
      this.editForm.get('clientLocationID').setValue(this.projects[index].clientLocationID);

      this.editIndex = index;
    });
  }

  onUpdateClick() {
    if (this.editForm.pristine) return;
    if (this.editForm.valid) {
      this.projectsService
        .updateProject(this.editForm.value)
        .pipe(take(1))
        .subscribe((response: Project) => {
          let p: Project | any = {};
          p.projectID = response.projectID;
          p.projectName = response.projectName;
          p.dateOfStart = response.dateOfStart;
          p.teamSize = response.teamSize;
          p.active = response.active;
          p.status = response.status;
          p.clientLocationID = response.clientLocationID;
          p.clientLocation = response.clientLocation;

          this.projects[this.editIndex] = p;

          this.editForm.reset();

          this.editProjectFormCancel.nativeElement.click();
        });
    }
  }

  onDeleteClick(index: number) {
    this.deleteForm.reset();

    setTimeout(() => {
      this.deleteForm.get('projectID').setValue(this.projects[index].projectID);
      this.deleteForm.get('projectName').setValue(this.projects[index].projectName);

      this.deleteIndex = index;
    });
  }

  onDeleteConfirmClick() {
    this.projectsService
      .deleteProject(this.deleteForm.get('projectID').value)
      .pipe(take(1))
      .subscribe(() => {
        this.projects.splice(this.deleteIndex, 1);

        this.deleteForm.reset();

        this.calculateNoOfPages();
      });
  }

  calculateNoOfPages() {
    const filterPipe = new FilterPipe();
    const resultProjects = filterPipe.transform(this.projects, this.searchBy, this.searchText);
    const noOfPages = Math.ceil(resultProjects?.length / this.pageSize);

    this.pages = [];
    for (let i = 0; i < noOfPages; i++) {
      this.pages.push({ pageIndex: i });
    }

    this.currentPageIndex = noOfPages - 1;
  }

  onSearchTextChange() {
    this.calculateNoOfPages();
    if (!this.searchText) this.currentPageIndex = this.searchPageIndex;
    else this.currentPageIndex = 0;
  }

  onPageIndexClicked(pageIndex: number) {
    if (!this.searchText) {
      this.previousSearchPageIndex = pageIndex;
      this.searchPageIndex = pageIndex;
      this.currentPageIndex = pageIndex;
    }
    this.searchPageIndex = this.previousSearchPageIndex;
    this.currentPageIndex = pageIndex;
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }
}
