import { Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subscription, take } from 'rxjs';
import { TaskPriority } from '../../../models/task-priority';
import { TaskPrioritiesService } from '../../../services/task-priorities.service';
import { FilterPipe } from '../../../pipes/filter.pipe';

@Component({
  selector: 'app-task-priorities',
  templateUrl: './task-priorities.component.html',
  styleUrls: ['./task-priorities.component.scss'],
})
export class TaskPrioritiesComponent implements OnInit, OnDestroy {
  taskPriorities: TaskPriority[] = [];

  editIndex: number;
  deleteIndex: number;

  searchBy: string = '';
  searchText: string;
  sortBy: string = 'taskPriorityName';
  sortOrder: string = 'ASC';

  currentPageIndex: number = 0;
  searchPageIndex: number = 0;
  previousSearchPageIndex: number = 0;
  pages: any[] = [];
  pageSize: number = 7;

  newForm: FormGroup | any;
  editForm: FormGroup | any;
  deleteForm: FormGroup | any;

  @ViewChild('defaultTextBox_New') defaultTextBox_New: ElementRef;
  @ViewChild('defaultTextBox_Edit') defaultTextBox_Edit: ElementRef;
  @ViewChild('newTaskPriorityFormCancel') newTaskPriorityFormCancel: ElementRef;
  @ViewChild('editTaskPriorityFormCancel') editTaskPriorityFormCancel: ElementRef;

  subscription: Subscription;

  constructor(private taskPrioritiesService: TaskPrioritiesService, private formBuilder: FormBuilder) {}

  ngOnInit() {
    this.subscription = this.taskPrioritiesService.getTaskPriorities().subscribe((response: TaskPriority[]) => {
      this.taskPriorities = response;
      this.calculateNoOfPages();
    });

    this.newForm = this.formBuilder.group({
      taskPriorityID: this.formBuilder.control(null),
      taskPriorityName: this.formBuilder.control(null, [Validators.required, Validators.maxLength(50)]),
    });

    this.editForm = this.formBuilder.group({
      taskPriorityID: this.formBuilder.control(null),
      taskPriorityName: this.formBuilder.control(null, [Validators.required, Validators.maxLength(50)]),
    });

    this.deleteForm = this.formBuilder.group({
      taskPriorityID: this.formBuilder.control(null),
      taskPriorityName: this.formBuilder.control({ value: null, disabled: true }),
    });
  }

  calculateNoOfPages() {
    const filterPipe = new FilterPipe();
    const noOfPages = Math.ceil(
      filterPipe.transform(this.taskPriorities, this.searchBy, this.searchText).length / this.pageSize
    );
    this.pages = [];

    for (let i = 0; i < noOfPages; i++) {
      this.pages.push({ pageIndex: i });
    }

    this.currentPageIndex = 0;
  }

  onPageIndexClicked(ind: number) {
    if (!this.searchText && ind >= 0 && ind < this.pages.length) {
      this.previousSearchPageIndex = ind;
      this.searchPageIndex = ind;
      this.currentPageIndex = ind;
    }
    this.searchPageIndex = this.previousSearchPageIndex;
    this.currentPageIndex = ind;
  }

  onNewClick() {
    this.newForm.reset({ taskPriorityID: 0 });
    setTimeout(() => {
      this.defaultTextBox_New.nativeElement.focus();
    }, 10);
  }

  onSaveClick() {
    if (this.newForm.valid) {
      const found = this.taskPriorities.some(
        (el) =>
          el.taskPriorityName.split(/[ -]+/).join(' ').trim() ===
          this.newForm.value.taskPriorityName.split(/[ -]+/).join(' ').trim()
      );
      if (found) return alert('That task priority already exists!');

      this.newForm.value.taskPriorityName =
        this.newForm.value.taskPriorityName.charAt(0).toUpperCase().trimLeft() +
        this.newForm.value.taskPriorityName.split(/[ ]+/).join(' ').slice(1);

      this.taskPrioritiesService
        .insertTaskPriority(this.newForm.value)
        .pipe(take(1))
        .subscribe({
          next: (response) => {
            this.taskPriorities.push(response);
            this.taskPriorities = this.taskPriorities.sort((a, b) =>
              a.taskPriorityName > b.taskPriorityName ? 1 : a.taskPriorityName < b.taskPriorityName ? -1 : 0
            );

            this.newForm.reset();
            this.newTaskPriorityFormCancel.nativeElement.click();
            this.calculateNoOfPages();
          },
          error: (error) => {
            console.log(error);
          },
        });
    }
  }

  onEditClick(taskPriority: TaskPriority) {
    this.editForm.reset();

    setTimeout(() => {
      this.editForm.patchValue(taskPriority);
      this.editIndex = this.taskPriorities.indexOf(taskPriority);

      this.defaultTextBox_Edit.nativeElement.focus();
    }, 10);
  }

  onUpdateClick() {
    if (this.editForm.value) {
      const found = this.taskPriorities.some(
        (el) =>
          el.taskPriorityName.split(/[ -]+/).join(' ').trim() ===
          this.editForm.value.taskPriorityName.split(/[ -]+/).join(' ').trim()
      );
      if (found) return alert('That task priority already exists!');

      this.editForm.value.taskPriorityName =
        this.editForm.value.taskPriorityName.charAt(0).toUpperCase().trimStart() +
        this.editForm.value.taskPriorityName.split(/[ ]+/).join(' ').slice(1);

      this.taskPrioritiesService
        .updateTaskPriority(this.editForm.value)
        .pipe(take(1))
        .subscribe({
          next: (response: TaskPriority) => {
            this.taskPriorities[this.editIndex] = response;

            this.taskPriorities = this.taskPriorities.sort((a, b) =>
              a.taskPriorityName > b.taskPriorityName ? 1 : a.taskPriorityName < b.taskPriorityName ? -1 : 0
            );

            this.editForm.reset();
            this.editTaskPriorityFormCancel.nativeElement.click();
          },
          error: (error) => {
            console.log(error);
          },
        });
    }
  }

  onDeleteClick(taskPriority: TaskPriority) {
    this.deleteForm.reset();

    setTimeout(() => {
      this.deleteForm.patchValue(taskPriority);
      this.deleteIndex = this.taskPriorities.indexOf(taskPriority);
    }, 10);
  }

  onDeleteConfirmClick() {
    this.taskPrioritiesService
      .deleteTaskPriority(this.deleteForm.value.taskPriorityID)
      .pipe(take(1))
      .subscribe({
        next: () => {
          this.taskPriorities.splice(this.deleteIndex, 1);

          this.deleteForm.reset();
          this.calculateNoOfPages();
        },
        error: (error) => {
          console.log(error);
        },
      });
  }

  onSearchTextChange() {
    this.calculateNoOfPages();
    if (!this.searchText) this.currentPageIndex = this.searchPageIndex;
    else this.currentPageIndex = 0;
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }
}
