import { Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subscription, take } from 'rxjs';
import { Country } from '../../../models/country';
import { CountriesService } from '../../../services/countries.service';
import { FilterPipe } from '../../../pipes/filter.pipe';

@Component({
  selector: 'app-countries',
  templateUrl: './countries.component.html',
  styleUrls: ['./countries.component.scss'],
})
export class CountriesComponent implements OnInit, OnDestroy {
  countries: Country[] = [];

  editIndex: number;
  deleteIndex: number;

  searchBy: string = '';
  searchText: string;
  sortBy: string = 'countryName';
  sortOrder: string = 'ASC';

  currentPageIndex: number = 0;
  searchPageIndex: number = 0;
  previousSearchPageIndex: number = 0;
  pages: any[] = [];
  pageSize: number = 7;

  newForm: FormGroup | any;
  editForm: FormGroup | any;
  deleteForm: FormGroup | any;

  @ViewChild('defaultTextBox_New') defaultTextBox_New: ElementRef;
  @ViewChild('defaultTextBox_Edit') defaultTextBox_Edit: ElementRef;
  @ViewChild('newCountryFormCancel') newCountryFormCancel: ElementRef;
  @ViewChild('editCountryFormCancel') editCountryFormCancel: ElementRef;

  subscription: Subscription;

  constructor(private countriesService: CountriesService, private formBuilder: FormBuilder) {}

  ngOnInit() {
    this.subscription = this.countriesService.getCountries().subscribe((response: Country[]) => {
      this.countries = response;
      this.calculateNoOfPages();
    });

    this.newForm = this.formBuilder.group({
      countryID: this.formBuilder.control(null),
      countryName: this.formBuilder.control(null, [Validators.required, Validators.maxLength(50)]),
    });

    this.editForm = this.formBuilder.group({
      countryID: this.formBuilder.control(null),
      countryName: this.formBuilder.control(null, [Validators.required, Validators.maxLength(50)]),
    });

    this.deleteForm = this.formBuilder.group({
      countryID: this.formBuilder.control(null),
      countryName: this.formBuilder.control({ value: null, disabled: true }, [Validators.required]),
    });
  }

  calculateNoOfPages() {
    const filterPipe = new FilterPipe();
    const noOfPages = Math.ceil(
      filterPipe.transform(this.countries, this.searchBy, this.searchText).length / this.pageSize
    );
    this.pages = [];

    for (let i = 0; i < noOfPages; i++) {
      this.pages.push({ pageIndex: i });
    }

    this.currentPageIndex = 0;
  }

  onPageIndexClicked(ind: number) {
    if (!this.searchText && ind >= 0 && ind < this.pages.length) {
      this.previousSearchPageIndex = ind;
      this.searchPageIndex = ind;
      this.currentPageIndex = ind;
    }
    this.searchPageIndex = this.previousSearchPageIndex;
    this.currentPageIndex = ind;
  }

  onNewClick() {
    this.newForm.reset({ countryID: 0 });
    setTimeout(() => {
      this.defaultTextBox_New.nativeElement.focus();
    }, 10);
  }

  onSaveClick() {
    if (this.newForm.valid) {
      const found = this.countries.some(
        (el) =>
          el.countryName.split(/[ -]+/).join(' ').trim() ===
          this.newForm.value.countryName.split(/[ -]+/).join(' ').trim()
      );
      if (found) return alert('That country already exists!');

      this.newForm.value.countryName =
        this.newForm.value.countryName.charAt(0).toUpperCase().trimLeft() +
        this.newForm.value.countryName.split(/[ ]+/).join(' ').slice(1);

      this.countriesService
        .insertCountry(this.newForm.value)
        .pipe(take(1))
        .subscribe({
          next: (response) => {
            const p: Country | any = {};
            p.countryID = response.countryID;
            p.countryName = response.countryName;

            this.countries.push(p);
            this.countries = this.countries.sort((a, b) =>
              a.countryName > b.countryName ? 1 : a.countryName < b.countryName ? -1 : 0
            );

            this.newForm.reset();
            this.newCountryFormCancel.nativeElement.click();
            this.calculateNoOfPages();
          },
          error: (error) => {
            console.log(error);
          },
        });
    }
  }

  onEditClick(country: Country) {
    this.editForm.reset();

    setTimeout(() => {
      this.editForm.patchValue(country);
      this.editIndex = this.countries.indexOf(country);

      this.defaultTextBox_Edit.nativeElement.focus();
    }, 10);
  }

  onUpdateClick() {
    if (this.editForm.valid) {
      const found = this.countries.some(
        (el) =>
          el.countryName.split(/[ -]+/).join(' ').trim() ===
          this.editForm.value.countryName.split(/[ -]+/).join(' ').trim()
      );
      if (found) return alert('That country already exists!');

      this.editForm.value.countryName =
        this.editForm.value.countryName.charAt(0).toUpperCase().trimStart() +
        this.editForm.value.countryName.split(/[ ]+/).join(' ').slice(1);

      this.countriesService
        .updateCountry(this.editForm.value)
        .pipe(take(1))
        .subscribe({
          next: (response: Country) => {
            this.countries[this.editIndex] = response;

            this.countries = this.countries.sort((a, b) =>
              a.countryName > b.countryName ? 1 : a.countryName < b.countryName ? -1 : 0
            );

            this.editForm.reset();
            this.editCountryFormCancel.nativeElement.click();
          },
          error: (error) => {
            console.log(error);
          },
        });
    }
  }

  onDeleteClick(country: Country) {
    this.deleteForm.reset();

    setTimeout(() => {
      this.deleteForm.patchValue(country);
      this.deleteIndex = this.countries.indexOf(country);
    }, 10);
  }

  onDeleteConfirmClick() {
    this.countriesService
      .deleteCountry(this.deleteForm.value.countryID)
      .pipe(take(1))
      .subscribe({
        next: () => {
          this.countries.splice(this.deleteIndex, 1);

          this.deleteForm.reset();
          this.calculateNoOfPages();
        },
        error: (error) => {
          console.log(error);
        },
      });
  }

  onSearchTextChange() {
    this.calculateNoOfPages();
    if (!this.searchText) this.currentPageIndex = this.searchPageIndex;
    else this.currentPageIndex = 0;
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }
}
