import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { Observable, Subscription, mergeMap, of, take, tap } from 'rxjs';
import { Papa } from 'ngx-papaparse';
import { Project } from '../../../models/project';
import { ProjectsService } from '../../../services/projects.service';
import { WorldCitiesCacheService } from '../../../cache-services/world-cities-cache.service';

@Component({
  selector: 'app-project-details',
  templateUrl: './project-details.component.html',
  styleUrls: ['./project-details.component.scss'],
  providers: [ProjectsService],
})
export class ProjectDetailsComponent implements OnInit, OnDestroy {
  project: Project;
  country: string;

  subscription: Subscription;

  constructor(
    private activatedRoute: ActivatedRoute,
    private projectsService: ProjectsService,
    private httpClient: HttpClient,
    private papa: Papa,
    private cacheService: WorldCitiesCacheService
  ) {}

  getData(): Observable<any> {
    const cachedData = this.cacheService.get('cachedData');
    if (cachedData) {
      return of(cachedData);
    } else {
      return this.httpClient.get('./assets/worldcities.csv', { responseType: 'text' }).pipe(
        tap((data) => {
          this.cacheService.set('cachedData', data);
        })
      );
    }
  }

  ngOnInit() {
    this.subscription = this.activatedRoute.params
      .pipe(
        mergeMap((params) => {
          return this.projectsService.getProjectByProjectID(params['projectid']);
        })
      )
      .subscribe((proj: Project) => {
        this.getData()
          .pipe(take(1))
          .subscribe((csv: any) => {
            let result = this.papa.parse(csv, { header: true, dynamicTyping: true });
            var re = new RegExp(proj.clientLocation.clientLocationName, 'y');
            this.country = result.data.find((c: any) => c.city?.match(re))?.country;

            this.project = proj;
          });
      });
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
}
