import { Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subscription, take } from 'rxjs';
import { TaskStatus } from '../../../models/task-status';
import { TaskStatusesService } from '../../../services/task-statuses.service';
import { FilterPipe } from '../../../pipes/filter.pipe';

@Component({
  selector: 'app-task-status',
  templateUrl: './task-status.component.html',
  styleUrls: ['./task-status.component.scss'],
})
export class TaskStatusComponent implements OnInit, OnDestroy {
  taskStatuses: TaskStatus[] = [];

  editIndex: number;
  deleteIndex: number;

  searchBy: string = '';
  searchText: string;
  sortBy: string = 'taskStatusName';
  sortOrder: string = 'ASC';

  currentPageIndex: number = 0;
  searchPageIndex: number = 0;
  previousSearchPageIndex: number = 0;
  pages: any[] = [];
  pageSize: number = 7;

  newForm: FormGroup | any;
  editForm: FormGroup | any;
  deleteForm: FormGroup | any;

  @ViewChild('defaultTextBox_New') defaultTextBox_New: ElementRef;
  @ViewChild('defaultTextBox_Edit') defaultTextBox_Edit: ElementRef;
  @ViewChild('newTaskStatusFormCancel') newTaskStatusFormCancel: ElementRef;
  @ViewChild('editTaskStatusFormCancel') editTaskStatusFormCancel: ElementRef;

  subscription: Subscription;

  constructor(private taskStatusesService: TaskStatusesService, private formBuilder: FormBuilder) {}

  ngOnInit() {
    this.subscription = this.taskStatusesService.getTaskStatuses().subscribe((response: TaskStatus[]) => {
      this.taskStatuses = response;
      this.calculateNoOfPages();
    });

    this.newForm = this.formBuilder.group({
      taskStatusID: this.formBuilder.control(null),
      taskStatusName: this.formBuilder.control(null, [Validators.required, Validators.maxLength(50)]),
    });

    this.editForm = this.formBuilder.group({
      taskStatusID: this.formBuilder.control(null),
      taskStatusName: this.formBuilder.control(null, [Validators.required, Validators.maxLength(50)]),
    });

    this.deleteForm = this.formBuilder.group({
      taskStatusID: this.formBuilder.control(null),
      taskStatusName: this.formBuilder.control({ value: null, disabled: true }),
    });
  }

  calculateNoOfPages() {
    const filterPipe = new FilterPipe();
    const noOfPages = Math.ceil(
      filterPipe.transform(this.taskStatuses, this.searchBy, this.searchText).length / this.pageSize
    );
    this.pages = [];

    for (let i = 0; i < noOfPages; i++) {
      this.pages.push({ pageIndex: i });
    }

    this.currentPageIndex = 0;
  }

  onPageIndexClicked(ind: number) {
    if (!this.searchText && ind >= 0 && ind < this.pages.length) {
      this.previousSearchPageIndex = ind;
      this.searchPageIndex = ind;
      this.currentPageIndex = ind;
    }
    this.searchPageIndex = this.previousSearchPageIndex;
    this.currentPageIndex = ind;
  }

  onNewClick() {
    this.newForm.reset({ taskStatusID: 0 });
    setTimeout(() => {
      this.defaultTextBox_New.nativeElement.focus();
    }, 10);
  }

  onSaveClick() {
    if (this.newForm.valid) {
      const found = this.taskStatuses.some(
        (el) =>
          el.taskStatusName.split(/[ -]+/).join(' ').trim() ===
          this.newForm.value.taskStatusName.split(/[ -]+/).join(' ').trim()
      );
      if (found) return alert('That task status already exists!');

      this.newForm.value.taskStatusName =
        this.newForm.value.taskStatusName.charAt(0).toUpperCase().trimLeft() +
        this.newForm.value.taskStatusName.split(/[ ]+/).join(' ').slice(1);

      this.taskStatusesService
        .insertTaskStatus(this.newForm.value)
        .pipe(take(1))
        .subscribe({
          next: (response) => {
            this.taskStatuses.push(response);
            this.taskStatuses = this.taskStatuses.sort((a, b) =>
              a.taskStatusName > b.taskStatusName ? 1 : a.taskStatusName < b.taskStatusName ? -1 : 0
            );

            this.newForm.reset();
            this.newTaskStatusFormCancel.nativeElement.click();
            this.calculateNoOfPages();
          },
          error: (error) => {
            console.log(error);
          },
        });
    }
  }

  onEditClick(taskStatus: TaskStatus) {
    this.editForm.reset();

    setTimeout(() => {
      this.editForm.patchValue(taskStatus);
      this.editIndex = this.taskStatuses.indexOf(taskStatus);

      this.defaultTextBox_Edit.nativeElement.focus();
    }, 10);
  }

  onUpdateClick() {
    if (this.editForm.value) {
      const found = this.taskStatuses.some(
        (el) =>
          el.taskStatusName.split(/[ -]+/).join(' ').trim() ===
          this.editForm.value.taskStatusName.split(/[ -]+/).join(' ').trim()
      );
      if (found) return alert('That task status already exists!');

      this.editForm.value.taskStatusName =
        this.editForm.value.taskStatusName.charAt(0).toUpperCase().trimStart() +
        this.editForm.value.taskStatusName.split(/[ ]+/).join(' ').slice(1);

      this.taskStatusesService
        .updateTaskStatus(this.editForm.value)
        .pipe(take(1))
        .subscribe({
          next: (response: TaskStatus) => {
            this.taskStatuses[this.editIndex] = response;

            this.taskStatuses = this.taskStatuses.sort((a, b) =>
              a.taskStatusName > b.taskStatusName ? 1 : a.taskStatusName < b.taskStatusName ? -1 : 0
            );

            this.editForm.reset();
            this.editTaskStatusFormCancel.nativeElement.click();
          },
          error: (error) => {
            console.log(error);
          },
        });
    }
  }

  onDeleteClick(taskStatus: TaskStatus) {
    this.deleteForm.reset();

    setTimeout(() => {
      this.deleteForm.patchValue(taskStatus);
      this.deleteIndex = this.taskStatuses.indexOf(taskStatus);
    }, 10);
  }

  onDeleteConfirmClick() {
    this.taskStatusesService
      .deleteTaskStatus(this.deleteForm.value.taskStatusID)
      .pipe(take(1))
      .subscribe({
        next: () => {
          this.taskStatuses.splice(this.deleteIndex, 1);

          this.deleteForm.reset();
          this.calculateNoOfPages();
        },
        error: (error) => {
          console.log(error);
        },
      });
  }

  onSearchTextChange() {
    this.calculateNoOfPages();
    if (!this.searchText) this.currentPageIndex = this.searchPageIndex;
    else this.currentPageIndex = 0;
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }
}
