import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, of, tap } from 'rxjs';
import { ClientLocation } from '../models/client-location';
import { ClientLocationsCacheService } from '../cache-services/client-locations-cache.service';

const API_BASE_URL = 'https://localhost:7036';

@Injectable({
  providedIn: 'root',
})
export class ClientLocationsService {
  constructor(private httpClient: HttpClient, private cacheService: ClientLocationsCacheService) {}

  getClientLocations(): Observable<ClientLocation[]> {
    const cachedData = this.cacheService.get('cachedData');
    if (cachedData) {
      return of(cachedData);
    } else {
      return this.httpClient
        .get<ClientLocation[]>(`${API_BASE_URL}/api/clientlocations`, { responseType: 'json' })
        .pipe(
          tap((data) => {
            this.cacheService.set('cachedData', data);
          })
        );
    }
  }

  getClientLocationsByClientLocationID(clientLocationID: number): Observable<ClientLocation> {
    return this.httpClient.get<ClientLocation>(
      `${API_BASE_URL}/api/clientlocations/searchbyclientlocationid/${clientLocationID}`,
      { responseType: 'json' }
    );
  }

  insertClientLocation(newClientLocation: ClientLocation): Observable<ClientLocation> {
    return this.httpClient.post<ClientLocation>(`${API_BASE_URL}/api/clientlocations`, newClientLocation, {
      responseType: 'json',
    });
  }

  updateClientLocation(existingClientLocation: ClientLocation): Observable<ClientLocation> {
    return this.httpClient.put<ClientLocation>(`${API_BASE_URL}/api/clientlocations`, existingClientLocation, {
      responseType: 'json',
    });
  }

  deleteClientLocation(clientLocationID: number): Observable<string> {
    return this.httpClient.delete<string>(`${API_BASE_URL}/api/clientlocations?ClientLocationID=${clientLocationID}`);
  }
}
